<template>
  <ContentBase>
      <div class="row">
        <div class="col-3">
          <UserProfileInfo @follow="follow" @unfollow="unfollow" :user="user" />
          <UserProfileWrite v-if="user.id == $store.state.user.id" @post_a_post="post_a_post" />
        </div>
        <div class="col-9">
          <UserProfilePosts @delete_a_post="delete_a_post" :user="user" :posts="posts" />
        </div>
      </div>
  </ContentBase>
</template>

<script>
import ContentBase from '../components/ContentBase'
import UserProfileInfo from '../components/UserProfileInfo';
import UserProfilePosts from '../components/UserProfilePosts';
import UserProfileWrite from '../components/UserProfileWrite';
import { reactive } from 'vue';
import { useRoute  } from 'vue-router';
import $ from 'jquery';
import { useStore } from 'vuex';

export default {
  name: 'UserList',
  components: {
      ContentBase,
      UserProfileInfo,
      UserProfilePosts,
      UserProfileWrite
  },
  setup() {
    const route = useRoute ();
    const userId = parseInt(route.params.userId);
    const user = reactive({});
    const store = useStore();

    $.ajax({
      url: "https://app165.acapp.acwing.com.cn/myspace/getinfo/",
      type: "get",
      data: {
        user_id: userId,
        me_id: store.state.user.id,
      },
      success(resp) {
        user.id = resp.id;
        user.username = resp.username;
        user.photo = resp.photo;
        user.followerCount = resp.followerCount;
        user.is_followed = resp.is_followed;
      }
    })

    const posts = reactive({
      count: 0,
      posts: [],
    });

    $.ajax({
      url: "https://app165.acapp.acwing.com.cn/myspace/post/",
      type: "GET",
      data: {
        user_id: userId,
      },
      headers: {"Authorization": 'Bearer ' + store.state.user.accessToken},
      success(resp) {
        posts.count = resp.length;
        posts.posts = resp;
      },
    });
    
    const follow = () => {
      if (user.is_followed) return;
      user.is_followed = true;
      user.followerCount ++ ;
    };

    const unfollow = () => {
      if (!user.is_followed) return;
      user.is_followed = false;
      user.followerCount -- ;
    };

    const post_a_post = (content) => {
      posts.count ++ ;
      posts.posts.unshift({
        id: posts.count,
        userId: 1,
        content: content,
      })
    };

    const delete_a_post = (postId) => {
      posts.count -- ;
      posts.posts = posts.posts.filter(post => post.id !== postId);
    }

    return {
      user,
      follow,
      unfollow,
      posts,
      post_a_post,
      delete_a_post,
    }
  }
}
</script>

