<template>
    <div class="card">
        <div class="card-body">
            <div v-for="post in posts.posts" :key="post.id">
                <div class="card single-post">
                    <div class="card-body">
                        {{ post.content }}
                        <button v-if="is_me" @click="delete_a_post(post.id)" type="button" class="btn btn-danger btn-sm">删除</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
import { computed } from 'vue';
import { useStore } from 'vuex';

export default {
    name: "UserProfilePosts",
    props: {
        posts: {
            type: Object,
            required: true,
        },
        user: {
            type: Object,
            required: true,
        }
    },
    setup(props, context) {
        const store = useStore();

        const delete_a_post = (postId) => {
            $.ajax({
                url: `https://app165.acapp.acwing.com.cn/myspace/post/`,
                type: "DELETE",
                data: {
                    post_id: postId,
                },
                headers: {'Authorization': "Bearer " + store.state.user.accessToken},
                success(resp) {
                    if (resp.result === "success") {
                        context.emit("delete_a_post", postId);
                    }
                },
            });
        };

        let is_me = computed(() => store.state.user.id === props.user.id);

        return {
            is_me,
            delete_a_post,
        }
    }
}
</script>


<style scoped>
.single-post {
    margin-bottom: 10px;
}

.single-post:hover {
    box-shadow: 2px 2px 5px lightgray;
    transition: 500ms;
}

button {
    float: right;
    padding: 2px 4px;
    font-size: 12px;
}
</style>