<template>
  <ContentBase>
      <div class="card single-user" v-for="user in users" :key="user.id" @click="go_to_user_profile(user.id)">
        <div class="card-body">
          <div class="row">
            <div class="col-1 photo">
              <img class="img-fluid" :src="user.photo" alt="">
            </div>
            <div class="col-11">
              <div class="username">{{ user.username }}</div>
              <div class="follower-count">粉丝：{{ user.followerCount }}</div>
            </div>
          </div>
        </div>
      </div>
  </ContentBase>
</template>

<script>
import ContentBase from '../components/ContentBase'
import router from '@/router/index';
import { ref } from 'vue';
import $ from 'jquery';
import { useStore } from 'vuex';

export default {
  name: 'UserList',
  components: {
      ContentBase,
  },
  setup() {
    const store = useStore();

    const go_to_user_profile = (userId) => {
      if (store.state.user.is_login) {
        router.push({name: 'userprofile', params: {userId: userId}});
      } else {
        router.push({name: "login"});
      }
    }

    let users = ref(null);

    $.ajax({
      url: "https://app165.acapp.acwing.com.cn/myspace/userlist/",
      type: "get",
      success: resp => {
        users.value = resp;
      },
    });

    return {
      users,
      go_to_user_profile,
    }
  }
}
</script>

<style scoped>
.photo {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

img {
  border-radius: 50%;
}

.single-user {
  margin-bottom: 20px;
  cursor: pointer;
}

.single-user:hover {
  box-shadow: 2px 2px 5px lightgrey;
  transition: 500ms;
}

.username {
  font-weight: bold;
  height: 50%;
}

.follower-count {
  height: 50%;
  font-size: 1rem;
  color: grey;
}
</style>